<template>
  <div class="main">
    <Top></Top>
    <Logo></Logo>
    <section id="content">
      <!-- 主體內容 -->
      <div class="main type-area">
        <SilderBar :choose-item="silderBarItem"></SilderBar>
        <div class="mainPart">
          <!-- 我的收入 -->
          <div class="main_income">
            <div class="main_in_title">我的收入</div>
            <div class="main_in_details">
              <div class="single">
                <div class="singleTit">累計收益</div>
                <div class="singleCon">5,264,800</div>
              </div>
              <div class="single">
                <div class="singleTit">製作中資產(元)</div>
                <div class="singleCon">193,500</div>
              </div>
              <div class="single">
                <div class="singleTit">參與項目</div>
                <div class="singleCon">
                  185
                  <span>個</span>
                </div>
              </div>
              <div class="single">
                <div class="singleTit">累計收益</div>
                <div class="singleCon">
                  27
                  <span>個</span>
                </div>
              </div>
              <div class="single">
                <el-progress
                  type="circle"
                  :percentage="18"
                  :stroke-width="progress.strokeWidth"
                  :width="progress.width"
                  :color="progress.color"
                ></el-progress>
              </div>
            </div>
          </div>
          <!-- 分割線 -->
          <div class="main_splitLine"></div>
          <!-- 接單管理 -->
          <div class="main_manage">
            <div class="main_ma_title">
              <div class="ma_tit_icon"></div>
              <div>接單管理</div>
            </div>
            <div class="main_ma_classify">
              <div
                :class="{active:orderStatus == 'all'}"
                @click="orderStatus = 'all'"
              >全部({{statusNum.all}})</div>
              <span class="line"></span>
              <div
                :class="{active:orderStatus == 'progress'}"
                @click="orderStatus = 'progress'"
              >進行中({{statusNum.progress}})</div>
              <span class="line"></span>
              <div
                :class="{active:orderStatus == 'win'}"
                @click="orderStatus = 'win'"
              >中標({{statusNum.win}})</div>
              <span class="line"></span>
              <div
                :class="{active:orderStatus == 'payment'}"
                @click="orderStatus = 'payment'"
              >待收款({{statusNum.payment}})</div>
              <span class="line"></span>
              <div
                :class="{active:orderStatus == 'evaluation'}"
                @click="orderStatus = 'evaluation'"
              >待評價({{statusNum.evaluation}})</div>
              <span class="line"></span>
              <div
                :class="{active:orderStatus == 'finished'}"
                @click="orderStatus = 'finished'"
              >已結束({{statusNum.finished}})</div>
            </div>
            <div class="main_ma_list">
              <!-- listStar -->
              <div
                class="ma_list_single"
                :class="{winBidding: item.winBidding}"
                v-for="(item, index) in orderList"
                :key="index"
              >
                <div class="list_sin_left">
                  <div class="sin_left_scope">{{item.scope}}</div>
                  <div class="sin_left_info">
                    <div class="left_info_item">
                      <div class="formNum">{{item.formNum}}</div>
                      <div class="state">
                        <div v-for="(itemS, index) in item.state"  :key="index">{{itemS}}</div>
                      </div>
                    </div>
                    <div class="left_info_item">
                      <div class="price">
                        <div class="kind">{{item.price[0]}}</div>
                        <div class="num">{{item.price[1]}}</div>
                      </div>
                      <div class="unitNum">{{item.unitNum}}個單位已參與</div>
                    </div>
                  </div>
                </div>
                <div class="list_sin_right">
                  <div class="sin_right_left">
                    <div :class="{fail: item.current.status == false }" class="current">
                      <div>{{item.current.text}}{{item.current.remainDate | formatDate}}</div>
                    </div>
                  </div>
                  <div class="sin_right_right" :class="item.status.en">
                    <div class="status">{{item.status.ch}}</div>
                    <div class="btn">
                      <router-link :to="item.href">
                        {{item.status.btnText}}
                      </router-link>
                      <!-- <a :href="item.href">{{item.status.btnText}}</a> -->
                    </div>
                  </div>
                </div>
              </div>
              <!-- listEnd -->
              <!-- 分頁 -->
              <div class="ma_list_page">
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="100"
                  prev-text="上一頁"
                  next-text="下一頁"
                ></el-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>

<script>
import Top from "../../components/Top"
import Logo from "../../components/Logo"
import Footer from "../../components/Footer"

import SilderBar from '../../components/personal/SilderBar'

export default {
  data: function() {
    return {
      // 環形進度條
      progress: {
        strokeWidth: 12,
        width: 100,
        color: "#0096FF"
      },
      // 選中的訂單狀態
      orderStatus: "all", // all progress production win payment evaluation finished
      // 狀態值
      statusNum: {
        all: "59",
        progress: "2",
        production: "0",
        win: "5",
        payment: "1",
        evaluation: "0",
        finished: "53"
      },
      // 數據
      orderList: [
        {
          scope: "檢",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["￥", "50000"],
          unitNum: "16",
          current: {
            status: true,
            // time:'投标剩余15天17小时5分39秒'
            startDate: "1568023104000",
            endDate: "1569044741000",
            remainDate: "",
            text: ""
          },
          status: {
            en: "progress",
            ch: "進行中",
            btnText: "詳情"
          },
          winBidding: false,
          href: ""
        },
        {
          scope: "治",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["$", "50000"],
          unitNum: "26",
          current: {
            status: true,
            startDate: "1558023104000",
            endDate: "1578023104000",
            remainDate: "",
            text: ""
          },
          status: {
            en: "win",
            ch: "中標",
            btnText: "簽核協議"
          },
          winBidding: true,
          href: ""
        },
        {
          scope: "檢",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["￥", "50000"],
          unitNum: "31",
          current: {
            status: true,
            startDate: "1508823104000",
            endDate: "1570023104000",
            remainDate: "",
            text: ""
          },
          status: {
            en: "production",
            ch: "製作中",
            btnText: "交貨"
          },
          winBidding: true,
          href: ""
        },
        {
          scope: "檢",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["￥", "50000"],
          unitNum: "31",
          current: {
            status: true,
            startDate: "1408823104000",
            endDate: "1508023104000",
            remainDate: "",
            text: ""
          },
          status: {
            en: "payment",
            ch: "待收款",
            btnText: "確認收款"
          },
          winBidding: true,
          href: ""
        },
        {
          scope: "檢",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["￥", "50000"],
          unitNum: "25",
          current: {
            status: true,
            startDate: "1408823104000",
            endDate: "1508023104000",
            remainDate: "",
            text: ""
          },
          status: {
            en: "evaluation",
            ch: "待評價",
            btnText: "評價"
          },
          winBidding: true,
          href: ""
        },
        {
          scope: "檢",
          formNum: "需求單號A30000028",
          state: ["成都D23", "產品處"],
          price: ["￥", "50000"],
          unitNum: "31",
          current: {
            status: false,
            startDate: "1408823104000",
            endDate: "1508023104000",
            remainDate: "",
            text: "競標失敗"
          },
          status: {
            en: "finished",
            ch: "已結束",
            btnText: "詳情"
          },
          winBidding: false,
          href: ""
        }
      ],
      // 侧边栏选中条目
      silderBarItem: 'order'
    };
  },
  components: {
    Top,
    Logo,
    Footer,
    SilderBar
  },
  created() {
    // 更改href
    for (let i in this.orderList) {
      switch (this.orderList[i].status.en) {
        case "progress":
          this.orderList[i].href = "personal/details";
          break;
        case "win":
          this.orderList[i].href = "/contract";
          break;
        case "production":
          this.orderList[i].href = "";
          break;
        case "payment":
          this.orderList[i].href = "";
          break;
        case "evaluation":
          this.orderList[i].href = "personal/postEvaluation";
          break;
        case "finished":
          this.orderList[i].href = "personal/details";
          break;
      }
    }

    // 修改時間
    for (let i in this.orderList) {
      // var startDate = this.orderList[i].current.startDate;
      var endDate = this.orderList[i].current.endDate;
      // var nowDate = Date.parse(new Date())
      var nowDate = 1569044721000;
      var remainDate = endDate - nowDate > 0 ? endDate - nowDate : "";
      if (remainDate == "") return;
      remainDate = remainDate / 1000;
      this.orderList[i].current.remainDate = remainDate;
      var _this = this;
      setInterval(function() {
        _this.orderList[i].current.remainDate > 0
          ? _this.orderList[i].current.remainDate--
          : (_this.orderList[i].current.remainDate = 0);
      }, 1000);
    }
  },
  filters: {
    formatDate(val) {
      // console.log(val)
      if (val === 0) {
        return "時間已到";
      } else if (val == "") {
        return;
      } else {
        var dd,
          hh,
          mm,
          ss = null;
        dd = Math.floor(val / 60 / 60 / 24);
        hh = Math.floor((val / 60 / 60) % 24);
        mm = Math.floor((val / 60) % 60);
        ss = Math.floor(val % 60);
        dd = dd < 10 ? "0" + dd : dd;
        hh = hh < 10 ? "0" + hh : hh;
        mm = mm < 10 ? "0" + mm : mm;
        ss = ss < 10 ? "0" + ss : ss;
        return "時間剩餘" + dd + "天" + hh + "小時" + mm + "分鐘" + ss + "秒";
      }
    }
  }
};
</script>

